<template>
    <div style="margin: auto;">
        <u-input v-model="value7" type="textarea" :autosize="true"></u-input>
        <u-input v-model="value7" width="168px" height="126px" type="textarea" :skin="img13" rule="1,3||20,10,20,10" placeholder="Enter something..."></u-input>
        <u-input v-model="value7" width="80px" height="30px" placeholder="Enter something..."></u-input>
        <u-input v-model="value7" width="168px" color="red" height="42px" :skin="img13" rule="1,3||20,10,20,10||2"></u-input>
        <u-input v-model="value11" number width="168px" color="red" text-align="center" background-color="blue" height="42px"></u-input>
        <u-input type="password" width="168px" color="red" text-align="center" background-color="blue" height="42px"></u-input>
        <u-input :parser="onParser" number width="168px" color="red" text-align="center" height="42px"></u-input>
    </div>
</template>
<script>
    import img12 from './img/carddating/btn_on.png';
    import img13 from './img/usercard/btn_enter.png';
    export default {
        data () {
            return {
                value: '',
                value11: 0,
                value12: '',
                value13: '',
                select1: 'http',
                select2: 'com',
                select3: 'day',
                value7: '',
                img12: img12,
                img13: img13,
            };
        },
        methods: {
            onParser(val) {
                return ((parseInt(val) > 1314) && 1314) || val;
            }
        }
    };
</script>



